export default {
	data() {
		return {
			isShow: false
		};
	},
	methods: {
		async addUser(userData) {
			let userInfoResult = await wx.cloud.callFunction({
				name: "addUser",
				data: {
					loginUser: userData
				}
			})
			if (userInfoResult.result.issuccess) {
				console.log(11)
				//增加账户成功
				uni.switchTab({
					url: '/pages/tabbar/tabbar-1/tabbar-1'
				});

			} else {
				uni.showModal({
					title: '提示',
					content: userInfoResult.result.data,
					showCancel: false
				});
			}

		},
		getuserinfo(data) {
			this.addUser(data.detail.userInfo)

		}
	},
	async created() {
		try {
			let result = await wx.cloud.callFunction({
				name: "getUserId"
			})
			if (result.result.data.length == 0) {
				let userInfo = await uni.getUserInfo()
				console.log(userInfo)
				if (userInfo.length == 2) {
					//已经授权，直接添加到数据库
					this.addUser(userInfo[1].userInfo)

				} else {
					//没授权，弹出授权按钮
					this.isShow = true
				}

			} else {
				console.log(result)
				uni.switchTab({
					url: '/pages/tabbar/tabbar-1/tabbar-1'
				});
			}
		} catch (ex) {
			console.log(ex)
		}

	}
}
